import axios from 'axios'
import api from './api.js'
import jwt from './jwt.js'


const state = {
    logs: null,
    logsLoading: false
}
  
const mutations = {
    setLogs (state, logs) {
        state.logs = logs
    },
    setLogsLoading (state, loading) {
        state.logsLoading = loading
    }
}
  
const actions = {
    setLogs ({ commit }, logs) {
        commit('setLogs', logs)
    },
    fetchLogs({ commit }) {
        commit('setLogsLoading', true)
        return new Promise(function(resolve, reject) {
            axios({
                method: 'get',
                url: `${api.state.path}/api/logs`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then(response => {
                commit('setLogsLoading', false)
                if (response.status == 200) {
                    commit('setLogs', response.data)
                    resolve()
                } else {
                    commit('setLogs', 'erreur')
                    reject()
                }
            })
            .catch(e => {
                commit('setLogsLoading', false)
                commit('setLogs', 'erreur')
                reject()
            })
        })
    }
}
  
const getters = {
    logs: state => state.logs,
    logsLoading: state => state.logsLoading

}  

  export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }