<template>
    <header id="header" :class="shad"> <!-- header : menu en haut de la page -->
      <img src="@/assets/icon/book.svg" class="book">
      <h1 class="title">Les p'tites bios</h1>
      <nav class="menu">
        <router-link to="/" v-if="$route.name !=='home'">Accueil</router-link>
        <router-link :to="'/'+$route.params.person" v-if="$route.params.person && $route.params.artwork || $route.params.name ==''">{{person.name}}</router-link>
      </nav>
      <nav :class="['menu', $route.name !=='home'?'hideSm':'']" style="float:right">
        <router-link to="/login" v-if="!jwt">Se connecter</router-link>
        <router-link to="/register" v-if="!jwt">S'inscrire</router-link>
      </nav>
      <nav class="topBottom">
        <img src="@/assets/icon/up.svg" @click="up" :class="{'on':on !='top'}"><br>
        <img src="@/assets/icon/down.svg" @click="down"  :class="{'on': on!='bottom'}">
      </nav>
    </header> <!-- fin header -->
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
        on: "top",
        shad: false,
    }
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'level',
        ]),
        ...mapGetters('persons',[
        'getPerson',
        'getPersons',
        ]),
        ...mapGetters('artworks',[
        'getArtworks',
        ]),
        person: function () {
          if(this.getPersons.length!==0) {
              return this.getPersons.filter(person => person.slug == this.$route.params.person)[0]
          } else {
              return this.getPerson
          }
        }
    },
    created() {
      document.onscroll = this.checkScroll
    },
    methods: {
      checkScroll() {
        this.shad = (window.pageYOffset > 0)?'shad':'';
        if (window.scrollY < 200) {
          this.on = "top"
        } else if (window.scrollY > document.body.clientHeight-window.innerHeight-200) {
          this.on = "bottom"
        } else {
          this.on = "middle"
        }
      },
      up() {
        window.scrollTo(0, 0)
      },
      down() {
        window.scrollTo(0, document.body.clientHeight)
      }
    }

}
</script>
