import axios from 'axios'
import api from './api.js'
import jwt from './jwt.js'

const state = {
    usersLoading: true,
    users: {},
    currentUser: {}
}
  
const mutations = {
    setUsers (state, users) {
        state.users = users
    },
    setCurrentUser (state, user) {
        state.currentUser = user
    },
    setUsersLoading (state, loading) {
        state.usersLoading = loading
    }
}
  
const actions = {
    setUsers ({ commit }, users) {
        commit('setUsers', users)
    },
    setCurrentUser ({ commit }, user) {
        commit('setCurrentUser', user)
    },
    modifyUser ({ commit, dispatch }, user) {
        if(!jwt.state.current) return false
        return new Promise(function(resolve, reject) {
            commit('setUsersLoading', true)
            axios({
                method: 'patch',
                url: `${api.state.path}/api/users/${user.id}`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                },
                data: user
            })
            .then (response => {
                commit('setUsersLoading', false)
                commit('setUsers', response.data)
                resolve()
            })
            .catch (e => {
                commit('setUsersLoading', false)
                dispatch('alert/newAlert', {type:'error', content:e.response.data}, {root:true})
                reject()
            })
        })
    },
    fetchUsers({ commit, dispatch, state  }, force=false) {
        if(!jwt.state.current) return false
        return new Promise(function(resolve, reject) {
            if(state.users.length > 0 && !force) {
                resolve()
                return
            }
            commit('setUsersLoading', true)
            axios({
                method: 'get',
                url: `${api.state.path}/api/users`,
                headers: {
                    Authorization: `Bearer ${jwt.state.current}`
                }
            })
            .then (response => {
                commit('setUsersLoading', false)
                commit('setUsers', response.data)
                resolve()
            })
            .catch (e => {
                commit('setUsersLoading', false)
                dispatch('alert/newAlert', {type:'error', content:e.response.data}, {root:true})
                reject()
            })
        })
    }
}
  
  const getters = {
    currentUser: state => state.currentUser,
    usersLoading: state => state.usersLoading,
    users: state => state.users,
    usersNoValidated: state => state.users.filter(user => user.level==0),
    usersUser: state => state.users.filter(user => user.level==1),
    usersModo: state => state.users.filter(user => user.level==2),
    usersAdmin: state => state.users.filter(user => user.level>=3)
  }
  
  export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }