import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  root: '/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/views/Home.vue')
    },
    {
      path: '/info',
      component: () => import('@/views/Info/Index.vue'),
      children: [
        { path: 'legal', name: 'legal', component: () => import('@/views/Info/Legal.vue') },
        { path: 'inscription', component: () => import('@/views/Info/Inscription.vue') },
        { path: 'badges', name: 'badges', component: () => import('@/views/Info/Badges.vue') },
        { path: 'contact', name: 'contact', component: () => import('@/views/Info/Contact.vue') },
        { path: 'reclamation', name: 'reclamation', component: () => import('@/views/Info/Reclamation.vue') }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/Auth/Login.vue')
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('@/views/Auth/Register.vue')
    },
    {
      path: '/new/artwork',
      name: 'newArtwork',
      meta: {minLevelAuth:1},
      component: () => import('@/views/Artwork/New.vue')
    },
    {
      path: '/new/person',
      name: 'newPerson',
      meta: {minLevelAuth:1},
      component: () => import('@/views/Person/New.vue')
    },
    {
      path: '/error/:code',
      name: 'error',
      component: () => import('@/views/Error.vue')
    },
    {
      path: '/edit/intro',
      name: 'editIntro',
      meta: {minLevelAuth:3},
      component: () => import('@/views/Intro/Edit.vue')
    },
    {
      path: '/artwork/:artwork',
      name: 'showArtworkAlias',
      component: () => import('@/views/Artwork/Show.vue')
    },
    {
      path: '/dashboard',
      component: () => import('@/views/Dashboard/Index.vue'),
      meta: {minLevelAuth:3}, 
      children: [
        {path:'', component: () => import('@/views/Dashboard/Show.vue')},
        {path: 'trash', component: () => import('@/views/Dashboard/Trash.vue')},
        {path:'users', component: () => import('@/views/Dashboard/Users.vue')},
        {path:'logs', component: () => import('@/views/Dashboard/Logs.vue')}
      ]
    },
    {
      path: '/:person',
      component: () => import('@/views/Person/Index.vue'),
      children: [
        {name:'showPerson', path:'', component: () => import('@/views/Person/Show.vue')},
        {name:'editPerson', path:'edit', meta: {minLevelAuth:2}, component: () => import('@/views/Person/Edit.vue')},
        {name:'printPerson', path: 'print', component: () => import('@/views/Person/Print.vue')},
        {path: ':artwork', component: () => import('@/views/Artwork/Index.vue'), children: [
          {name:'showArtwork', path:'', component: () => import('@/views/Artwork/Show.vue')},
          {name:'editArtwork', path:'edit', meta: {minLevelAuth:2}, component: () => import('@/views/Artwork/Edit.vue')},
          {name:'printArtwork', path: 'print', component: () => import('@/views/Artwork/Print.vue')}
        ]}
      ]
    }
  ]
})
